import Skeleton from '@material-ui/lab/Skeleton';
import Typography from '@material-ui/core/Typography';
import React from 'react';
import Container from '@material-ui/core/Container';


//import DownloadIcon from '@material-ui/icons/Download';
import KeyboardArrowUpIcon from '@material-ui/icons/KeyboardArrowUp';
import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';
import FileDownloadIcon from '@mui/icons-material/FileDownload';

import IconButton from '@material-ui/core/IconButton';
import Box from '@material-ui/core/Box';
import Link from '@material-ui/core/Link';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import axios from 'axios';

//let previousOrders = [{order:8741283,price:10.56},{order:741829,price:8}]

export default function UserPreviousOrders(userToken){
    const [loading, setLoading] = React.useState(true)

    const [open, setOpen] = React.useState(false);

    const [previousOrders, setPreviousOrders] = React.useState(null)

    React.useEffect(() => {
        axios({
            method: "GET",
            /* url: 'https://kioscodiegodeleon.com:5000/workingupload', */
            url: 'http://localhost:5000/getUserOrders',
            //data: formData,
            headers: {
                "Content-Type": "application/json",
                //"idToken": idToken, 
                "idToken": 45641231,
                /* printSettingsObject: */
            },
            //onUploadProgress: data => {
              //Set the progress value to show the progress bar
              //console.log("axios changed upload progress, now:", ((data.loaded/data.total)*100).toFixed(0));
              //setStatefulUploadProgress(Number(((data.loaded/data.total)*100).toFixed(0)));
            //},
        }).then(async (response) => {

              console.log("response", response, "/n");
              console.log("response.data", response.data);

              setPreviousOrders(JSON.parse(response.data));
              setLoading(false);
        })      
      }, []);

    return(
        <Container>        
            <Box mt={"1em"}>
                <Typography variant="h4" >
                    Ultimos pedidos
                </Typography>
            </Box>

            {/* <Skeleton variant="text" />
            <Skeleton variant="circle" width={40} height={40} />
            <Skeleton variant="rect" width={210} height={118} /> */}
            {/* <Typography variant="h1">
                {loading ? <Skeleton animation="wave"/> : 'h1'}
            </Typography>
            <Skeleton />
            <Skeleton animation={false} />
            <Skeleton animation="wave" /> */}
            {/* <Paper> */}

            <Box mt={"1em"}> 
                {loading?
                    (<Skeleton variant="rect" width={"100%"} height={118} />)
                    :
                    (
                    <Paper>
                        <TableContainer>
                            <Table>
                                <TableHead>
                                    <TableRow>                                        
                                        <TableCell padding='none'style={{maxWidth:"0.5em"}} align="center"/>
                                        <TableCell padding='none'align="center">Pedido numero</TableCell>
                                        <TableCell padding='none'align="center">Fecha</TableCell>
                                        <TableCell padding='none'align="center">Precio</TableCell>
                                        <TableCell padding='none'align="center">Factura</TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    
                                    {previousOrders?(previousOrders.map((row)=>(
                                        
                                        <TableRow key={previousOrders.indexOf(row)}>
                                        
                                        {/* {
                                            console.log("key=previousOrders.indexOf(row)", previousOrders.indexOf(row))
                                        } */}
                                        
                                            <TableCell padding='none'style={{maxWidth:"0.5em"}} align="center">
                                                <IconButton
                                                    aria-label="expand row"
                                                    size="small"
                                                    onClick={() => setOpen(!open)}
                                                >
                                                    {open ? <KeyboardArrowUpIcon /> : <KeyboardArrowDownIcon />}
                                                </IconButton>
                                            </TableCell>
                                            <TableCell padding='none'align="center">{row.order}</TableCell>
                                            <TableCell padding='none'align="center">{row.date}</TableCell>
                                            <TableCell padding='none'align="center">{row.price}</TableCell>
                                            <TableCell padding='none'align="center">
                                                <IconButton>
                                                <FileDownloadIcon></FileDownloadIcon>
                                                </IconButton>
                                            </TableCell>
                                        </TableRow>
                                    ))):(
                                        <TableRow>
                                            Sin pedidos
                                        </TableRow>
                                    )
                                    }
                                </TableBody>
                            </Table>
                        </TableContainer>
                    </Paper>
                    )
                }
            </Box>
            

            
        </Container>
    )
}