import React from 'react';

import Fab from '@material-ui/core/Fab';
import { makeStyles } from '@material-ui/core/styles';
import ClearIcon from '@material-ui/icons/Clear';

const useStyles = makeStyles((theme) => ({
    customDivDeleteDocumentFabContainer: {        
        position: 'absolute',            
        top:'1px',
        left:'1px',        
        zIndex: '500',
        border:'none'
    },
    customFAB:{
        minHeight:'25px',
        minWidth:'25px',
        width:'25px',
        height:'25px',
        maxHeight:'25px',
        maxWidth:'25px',
        /* overflow:'hidden', */
        /* lineHeight:'1rem',
        fontSize:'1rem', */
    }
    
}
));

export default function DeleteDocumentButton(props){

    const classes = useStyles();

    return(
        <div className={classes.customDivDeleteDocumentFabContainer}>

            <Fab  color='secondary' size='small' variant='round' className={classes.customFAB} onClick={()=>props.passedDoucumentDeletionHandler(props.passUniqueKeyToProcessDeletion)}>
                <ClearIcon></ClearIcon>
            </Fab>

        </div>
    )
}