import React from 'react';

import { loadStripe } from "@stripe/stripe-js";

//import CircularProgress from '@material-ui/core/CircularProgress';
import Container from '@material-ui/core/Container';

//import CustomCheckoutButtonAndProductDisplay from './customCheckoutButton.js';

import CircularStatic from './customMadeCircularProgressWIthLabel.js'

import { makeStyles } from '@material-ui/core/styles';

const axios = require('axios');

const stripePromise = loadStripe("pk_test_TYooMQauvdEDq54NiTphI7jx");


const useStyles = makeStyles({
  mainContainer: {
    width:'100%',
    maxWidth:'100%',
    minWidth:'100%',
    
  },
  containerForChildren: {        
    display:'flex',
    alignContent:'center',
    alignItems:'center',
    alignSelf:'center',
    justifyContent:'center',
    justifySelf:'center',
    /* padding:'40px' */
    
  },
});

/* Flag to control that only one upload post is made*/
let handleUploadCalledAlreadyFlag = 0;

export default function PaymentGateway(props){

  

  let arrayWithAllFilesConfigSettings = props.arrayWithAllFilesConfigSettings;
  let statefulUserObject = props.statefulUserObject;
  let setStatefulUserObject = props.setStatefulUserObject;
  let firebase = props.firebase;
  let openServerAuthErrorNotification = props.openServerAuthErrorNotification;
  let setOpenServerAuthErrorNotification = props.setOpenServerAuthErrorNotification;
  
  let statefulUploadProgress = props.statefulUploadProgress;
  let setStatefulUploadProgress = props.setStatefulUploadProgress;

  const classes = useStyles();


  async function handleUpload (arrayWithAllFilesConfigSettings, statefulUserObject, firebase) {
    const stripe = await stripePromise;
    //console.log("inside handle upload")
    if(handleUploadCalledAlreadyFlag==false){
      handleUploadCalledAlreadyFlag = true;
      let formData = new FormData();
      let fileReader = new FileReader();        
      for(let i=0; i<arrayWithAllFilesConfigSettings.length;i++){
        formData.append("images",arrayWithAllFilesConfigSettings[i].rawFile);          
        
        /* let stringVersionOfFile = fileReader.readAsText(arrayWithAllFilesConfigSettings[i].rawFile);
        console.log("stringVersionOfFile", stringVersionOfFile) */
        
        let copiedFileKeyAndObject = JSON.parse(JSON.stringify(arrayWithAllFilesConfigSettings[i]))
      
        copiedFileKeyAndObject.rawFile.name = arrayWithAllFilesConfigSettings[i].rawFile.name;
        copiedFileKeyAndObject.rawFile.size = arrayWithAllFilesConfigSettings[i].rawFile.size;
        copiedFileKeyAndObject.rawFile.lastModified = arrayWithAllFilesConfigSettings[i].rawFile.lastModified;

        formData.append("printOptionsAndIdentificationString", JSON.stringify(copiedFileKeyAndObject))
      }

      let session;
      
      firebase.auth().currentUser.getIdToken(/* forceRefresh */ true).then(function(idToken) {

        setStatefulUploadProgress(0);
        // Send token to your backend via HTTPS
        axios({
            method: "POST",
            /* url: 'https://kioscodiegodeleon.com:5000/workingupload', */
            url: 'http://localhost:5000/workingupload',
            data: formData,
            headers: {
                "Content-Type": "multipart/form-data",
                "idToken": idToken /* 45641231 */, 
                /* printSettingsObject: */
            },
            onUploadProgress: data => {
              //Set the progress value to show the progress bar
              //console.log("axios changed upload progress, now:", ((data.loaded/data.total)*100).toFixed(0));
              setStatefulUploadProgress(Number(((data.loaded/data.total)*100).toFixed(0)));
            },
        })
            .then(async (response) => {

              console.log("response", response);
                
              session = await response.data;

              // When the customer clicks on the button, redirect them to Checkout.
              const result = await stripe.redirectToCheckout({
                sessionId: session.id,
              });

              if (result.error) {
                // If `redirectToCheckout` fails due to a browser or network
                // error, display the localized error message to your customer
                // using `result.error.message`.
              }
              
                    /* if (response.status === 200) {
                        console.log("Success, firm added")
                    } else {
                        console.log("Error occurred")
                    } */
                }
            ).catch(e => {
            console.log(e);
            setOpenServerAuthErrorNotification(true);
        })        
      }).catch(function(error) {
        // Handle error
        setOpenServerAuthErrorNotification(true);
      });
        
      
    }
    
  }
  if(handleUploadCalledAlreadyFlag!=true){    
    handleUpload(arrayWithAllFilesConfigSettings, statefulUserObject, firebase)
  }  
  return(
    <Container className={classes.mainContainer}>            
      <div className={classes.containerForChildren}>
        <h1 className={classes.uploadProgressCircle}>Uploading Files</h1>
      </div>
      <div className={classes.containerForChildren}>
        <CircularStatic className={classes.uploadProgressCircle} progress={statefulUploadProgress}></CircularStatic>    
      </div>
    </Container>      
    
    
  )
}