import SignInFirebase from "./signInFirebase"
import { makeStyles } from '@material-ui/core/styles';
import Avatar from '@material-ui/core/Avatar';
import Container from '@material-ui/core/Container';
import AccountView from './AccountView.js';

const useStyles = makeStyles((theme) => ({
    root: {
      display: 'flex',
      '& > *': {
        margin: theme.spacing(1),
      },
    },
    small: {
      width: theme.spacing(3),
      height: theme.spacing(3),
    },
    large: {
      width: theme.spacing(7),
      height: theme.spacing(7),
    },
}));

export default function ContainerOfAccountView(props){
    const classes = useStyles();

    const showLogInScreen = props.showLogInScreen
    const setShowLogInScreen = props.setShowLogInScreen
    const firebase = props.firebase
    const setOpenSuccessfulLogInNotification = props.setOpenSuccessfulLogInNotification
    const openAuthErrorNotification = props.openAuthErrorNotification
    const setOpenAuthErrorNotification =  props.setOpenAuthErrorNotification

    const signInOptional = false;
    const userIsLoggedIn = props.userIsLoggedIn

    const statefulUserObject = props.statefulUserObject

    return(        
      <div>
        {
          (!userIsLoggedIn)?(<SignInFirebase signInOptional={signInOptional} firebase={firebase} showLogInScreen={showLogInScreen} setShowLogInScreen={setShowLogInScreen} setOpenSuccessfulLogInNotification={setOpenSuccessfulLogInNotification} openAuthErrorNotification={openAuthErrorNotification} setOpenAuthErrorNotification={setOpenAuthErrorNotification}></SignInFirebase>):(<AccountView statefulUserObject={statefulUserObject}></AccountView>)
        }
      </div>
      )
}